import { c as connection } from "../../../../chunks/connection.js";
import { r as redirect } from "../../../../chunks/index.js";
const load = async ({ locals }) => {
  const { user } = locals;
  const events = await connection.findSoloEvents(user.participant_id);
  return {
    events
  };
};
const actions = {
  logout: async ({ cookies }) => {
    cookies.delete("session", {
      path: "/",
      httpOnly: true,
      sameSite: "strict",
      secure: false,
      //TODO : to be changed,
      maxAge: 60 * 60 * 24 * 3
    });
    throw redirect(302, "/");
  }
};
export {
  actions,
  load
};
